export default function StepPips({
                                     current,
                                     total,
                                 }: {
    current: number;
    total: number;
}) {
    return (
        <div className="flex gap-2">
            {Array.from({ length: total }).map((_, i) => (
                <div
                    key={i}
                    className={`w-2 h-2 rounded-full transition ${
                        current === i ? "bg-primary" : "bg-neutral/40"
                    }`}
                />
            ))}
        </div>
    );
}
